import React, { Component } from "react";
import HeaderCard from "../HeaderCard";
import CetakBarcodeHead from "./CetakBarcodeHead";
import { getListBarang } from "../actions/kodeBarangAction";
import { connect } from "react-redux";

class CetakBarcode extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
  }

  paddingNumberGW(str) {
    var pad = "0000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  paddingNumberNW(str) {
    var pad = "00000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  handleSubmit(data) {
    let feedback = `AEX0ARA3H001V001CS2#E4APM0PO3+00YE0IG1PH0A102000671Z##                            APSWKesther grosir LB`;
    feedback += `%2H0637V0187L0102P02S${data.kode_barang} - ${data.sub_kategori}`;
    feedback += `%2H0622V0158BG01067>H${
      data.kode_barang
    }X>C${this.paddingNumberGW(
      parseFloat(data.gw).toFixed(2),
      7
    )}${this.paddingNumberNW(parseFloat(data.nw).toFixed(2), 5)}`;
    feedback += `%2H0622V0087L0101P02S${data.kode_barang}X>C${this.paddingNumberGW(
      parseFloat(data.gw).toFixed(2),
      7
    )}${this.paddingNumberNW(parseFloat(data.nw).toFixed(2), 5)}`;
    feedback += `%2H0650V0058L0101P02S${"GW"}%2H0622V0065L0102P02S${parseFloat(
      data.gw || 0
    ).toFixed(2)}`;
    feedback += `%2H0650V0028L0101P02S${"NW"}%2H0622V0038L0102P02S${parseFloat(
      data.nw || 0
    ).toFixed(2)}`;
    feedback += `%2H0535V0058L0101P02S${"ONG"}%2H0497V0068L0102P02SRp. ${
      parseFloat(data.ongkos || 0).toLocaleString("id-ID") || 0
    }`;
    feedback += `%2H0515V0028L0101P02S${"T"}%2H0497V0038L0102P02S${parseFloat(
      data.tukaran || 0
    ).toFixed(2)}%`;
    if (data.kode_barang2 === undefined) {
      feedback += `&01NiceOvlZA/01~A0Q1Z##  ##`;
    } else {
      feedback += `%2H0304V0187L0102P02S${data.kode_barang2} - ${data.sub_kategori2}`;
      feedback += `%2H0277V0158BG01067>H${
        data.kode_barang2
      }X>C${this.paddingNumberGW(
        parseFloat(data.gw2).toFixed(2),
        7
      )}${this.paddingNumberNW(parseFloat(data.nw2).toFixed(2), 5)}`;
      feedback += `%2H0277V0087L0101P02S${
        data.kode_barang2
      }X>C${this.paddingNumberGW(
        parseFloat(data.gw2).toFixed(2),
        7
      )}${this.paddingNumberNW(parseFloat(data.nw2).toFixed(2), 5)}`;
      feedback += `%2H0306V0058L0101P02S${"GW"}%2H0277V0065L0102P02S${parseFloat(
        parseFloat(data.gw2).toFixed(2) || 0
      ).toFixed(2)}`;
      feedback += `%2H0306V0025L0101P02S${"NW"}%2H0277V0036L0102P02S${parseFloat(
        parseFloat(data.nw2).toFixed(2) || 0
      ).toFixed(2)}`;
      feedback += `%2H0194V0058L0101P02S${"ONG"}%2H0156V0068L0102P02SRp. ${
        parseFloat(data.ongkos2 || 0).toLocaleString("id-ID") || 0
      }`;
      feedback += `%2H0174V0025L0101P02S${"T"}%2H0156V0038L0102P02S${parseFloat(
        data.tukaran2 || 0
      ).toFixed(2)} %`;
      feedback += `&01NiceOvlZA/01~A0Q1Z##  ##`;
    }
    document.getElementById("myInput").value = feedback;
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("myInput").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_barcode.txt";
    document.body.appendChild(element);
    element.click();
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Cetak Barcode" />
          <div className="card-body">
            <div className="row">
              <div className="col-lg-12">
                {/* BARCODE 1 */}
                <CetakBarcodeHead
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
              <div className="d-none">
                <textarea id="myInput"></textarea>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(CetakBarcode);
